}
.games__time dt::after {
  width: 100%;
}
@media (max-width: 767px) {
  .games__time dt::after {
    width: 101%;
  }
}
.games__date dd, .games__time dd {
  margin: 0;
  font-size: 40px;
  font-weight: 700;
  line-height: 1.5;
}
@media (max-width: 1365px) {
  .games__date dd, .games__time dd {
    font-size: 24px;
    line-height: 1;
  }
}
.games__date dd {
  display: flex;
  margin-top: 3px;
}
@media (max-width: 1365px) {
  .games__date dd {
    margin-top: 2px;
  }
}
@media (max-width: 767px) {
  .games__date dd {
    margin-top: 3px;
  }
}
.games__time dd {
  line-height: 1.7;
}
@media (max-width: 1365px) {
  .games__time dd {
    line-height: 1;
    margin-top: 2px;
  }
}
@media (max-width: 767px) {
  .games__time dd {
    margin-top: 3px;
  }
}
.games__date span {
  margin-top: 11px;
  margin-left: 7px;
  font-size: 14px;
  line-height: 1.33;
  font-weight: 400;
  max-width: 40px;
}
@media (max-width: 1365px) {
  .games__date span {
    font-size: 10px;
    line-height: 1;
    margin-top: 2px;
    margin-left: 4px;
  }
}
.games__text {
  margin: 0;
  font-size: 18px;
  line-height: 1.33;
  font-weight: 300;
  letter-spacing: 0;
}
@media (max-width: 1365px) {
  .games__text {
    font-size: 18px;
    line-height: 1.33;
    letter-spacing: -0.06px;
  }
}
@media (max-width: 767px) {
  .games__text {
    font-size: 16px;
    line-height: 1.25;
  }
}
.games__button {
  margin: 56px 0 0 0;
}
@media (max-width: 1365px) {
  .games__button {
    margin-top: 21px;
  }
}
@media (max-width: 767px) {
  .games__button {
    margin-top: 19px;
  }
}
.games__image-wrapper {
  width: 100%;
  background-color: rgba(20, 54, 122, 0.5);
}
@media (max-width: 767px) {
  .games__image-wrapper img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    max-height: 292px;
  }
}

